import {
  SELECT_FOLDER,
  DESELECT_FOLDER,
  ADD_FOLDER,
  DOUBLE_CLICK_FOLDER,
  CLOSE_WINDOW,
  DRAG_ELEMENT,
  HIGHLIGHT_ELEMENT,
  TOGGLE_START_MENU,
  TOGGLE_SUB_MENU,
} from './actionsTypes';

const selectFolder = title => ({ type: SELECT_FOLDER, payload: title });

const deselectFolder = () => ({ type: DESELECT_FOLDER });

const addFolder = title => ({ type: ADD_FOLDER, payload: title });

const closeWindow = identifier => ({ type: CLOSE_WINDOW, payload: identifier });

const doubleClickFolder = (identifier, title) => ({ type: DOUBLE_CLICK_FOLDER, payload: {
  identifier,
  title,
} });

const dragElement = (identifier, top, left) => ({ type: DRAG_ELEMENT, payload: { identifier, top, left } });

const highlightElement = identifier => ({ type: HIGHLIGHT_ELEMENT, payload: identifier });

const toggleStartMenu = toggle => ({ type: TOGGLE_START_MENU, payload: toggle });

const toggleSubMenu = title => ({ type: TOGGLE_SUB_MENU, payload: title });

export {
  selectFolder,
  deselectFolder,
  addFolder,
  doubleClickFolder,
  closeWindow,
  dragElement,
  highlightElement,
  toggleStartMenu,
  toggleSubMenu,
};
