import React, { useState } from 'react';
import { connect } from 'react-redux';
import Folder from './folder';
import Window from './window';
import StartBar from './startBar';
import StartMenu from './startMenu';
import { deselectFolder, dragElement, highlightElement, toggleStartMenu } from '../actions/folderActions';

const App = ({
  deselectFolder,
  windowArray,
  toggle,
  folders,
  dragElement,
  highlightElement,
  toggleStartMenuAction,
}) => {
  const [element, setElement] = useState(null);
  const [offsetX, setOffsetX] = useState(null);
  const [offsetY, setOffsetY] = useState(null);
  const acceptedClasses = {
    'window-bar': true,
    'window-bar ': true,
    'window-bar window-bar--highlight': true,
    'window-title__title': true,
    'window-title__icon': true,
  };
  const deepClasses = {
    'window-title__title': true,
    'window-title__icon': true,
  };

  const handleMouseDown = (e) => {
    const className = e.target.className;
    let el;
    if (className !== 'folder') {
      deselectFolder();
    }
    if (className === 'app-container') {
      toggleStartMenuAction(false);
    }

    if (acceptedClasses[className]) {
      if (deepClasses[className]) {
        el = e.target.parentNode.parentNode;
        setElement(el);
      } else {
        el = e.target;
        setElement(e.target);
      }
      setOffsetX(e.clientX - el.getBoundingClientRect().left);
      setOffsetY(e.clientY - el.getBoundingClientRect().top);
      highlightElement(el.getAttribute('identifier'));
    }
  };

  const handleMouseMove = (e) => {
    if (!element) {
      return;
    }
    const left = `${e.clientX - offsetX}px`;
    const top = `${e.clientY - offsetY}px`;
    dragElement(element.getAttribute('identifier'), top, left);
  };

  const handleMouseUp = () => {
    setElement(null);
  };

  return (
    <div onMouseDown={e => handleMouseDown(e)} onMouseUp={() => handleMouseUp()} onMouseMove={e => handleMouseMove(e)} className="app-container">
      { folders.map(title => <Folder key={`folder-${title}`} title={title} identifier={`folder-${title}`} />) }
      { toggle ? <StartMenu /> : null }
      <StartBar />
      { windowArray.length ? windowArray.map(item => <Window title={item.title} key={`window-${item.title}`} identifier={`window-${item.title}`} />) : null }
    </div>
  );
};

const mapDispatchToProps = dispatch => ({
  deselectFolder: () => dispatch(deselectFolder()),
  dragElement: (key, top, left) => dispatch(dragElement(key, top, left)),
  highlightElement: identifier => dispatch(highlightElement(identifier)),
  toggleStartMenuAction: value => dispatch(toggleStartMenu(value)),
});

const mapStateToProps = state => ({
  windowArray: state.window,
  folders: state.folder.folders,
  toggle: state.menu.toggle,
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
