import {
  DOUBLE_CLICK_FOLDER,
  CLOSE_WINDOW,
  DRAG_ELEMENT,
} from '../actions/actionsTypes';
import Content from '../content';

const initialState = [];
export default (state = initialState, action) => {
  switch (action.type) {
    case DOUBLE_CLICK_FOLDER: {
      let folderExists = false;
      for (const item of state) {
        if (`folder-${item.title}` === action.payload.identifier) {
          folderExists = true;
        }
      }
      if (folderExists) {
        return state;
      }
      return [
        ...state, {
          identifier: `window-${action.payload.title}`,
          title: action.payload.title,
          content: Content[action.payload.title],
          position: {
            top: '200px',
            left: '200px',
          },
        },
      ];
    }
    case CLOSE_WINDOW:
      return state.filter(item => item.identifier !== action.payload);

    case DRAG_ELEMENT: {
      let index;
      for (let i = 0; i < state.length; i += 1) {
        if (state[i].identifier === action.payload.identifier) {
          index = i;
        }
      }
      const filteredWindow = state.splice(index, 1);
      const newState = [...state];
      newState.splice(index, 0, {
        ...filteredWindow[0],
        position: {
          top: action.payload.top,
          left: action.payload.left,
        },
      });
      return newState;
    }
    default:
      return state;
  }
};
